import AppModel from '../models/AppModel';
import AppView from '../views/AppView';


export default class App {
  constructor(url) {
    this.url = url;
  }

  async start() {
    const wrapInput = document.createElement('div');
    wrapInput.classList.add('wrap-input');
    const input = document.createElement('input');
    const btn = document.createElement('button');

    input.type = 'text';
    btn.innerHTML = 'search';
    document.body.appendChild(wrapInput);
    wrapInput.appendChild(input);
    wrapInput.appendChild(btn);

    let request = 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&type=video&part=snippet&maxResults=15&q=';

    btn.addEventListener('click', async () => {
      request += input.value;
      this.url = request;
      request += '&pageToken=';

      const model = new AppModel(this.url);
      const data = await model.getInfo();
      const view = new AppView(data);
      await view.render();
    });

    let count = 0;
    let clean = false;

    window.addEventListener('mouseup', async () => {
      const flag = document.getElementsByClassName('container').length;
      if (flag) {
        count += 1;
        if (count % 2 === 0) {
          this.url = request;

          if (clean) {
            request = request.substr(0, request.length - 6);
          }

          let model = new AppModel(this.url);
          const nextToken = await model.getNextToken();
          request += nextToken;
          this.url += request;


          model = new AppModel(request);
          const data = await model.getInfo();
          const view = new AppView(data);
          clean = true;

          await view.render();
        }
      }
    });
  }
}
