// import App from '../../controllers/App'


export default class AppView {
  constructor(card) {
    this.card = card;
  }

  render() {
    const flag = document.getElementsByClassName('container').length;

    if (flag > 0) {
      const oldContainer = document.querySelector('.container');

      const title = this.card.map(card => card.title);
      const img = this.card.map(card => card.thumbnails.medium.url);
      const channelTitle = this.card.map(card => card.channelTitle);
      const published = this.card.map(card => card.publishedAt.substring(0, 10));
      const description = this.card.map(card => card.description);

      for (let i = 0; i < 15; i += 1) {
        const clipWrap = document.createElement('div');
        clipWrap.classList.add('clip-wrap');
        clipWrap.innerHTML = `
        <img class = 'prev-img' src=${img[i]}>
        <div class = 'clip-title'><p>${title[i]}</p></div>
        <p class = 'clip-chanel'>${channelTitle[i]}</p>
        <p class = 'clip-date'>${published[i]}</p>
        <p class = 'clip-description'>${description[i]}</p>`;
        oldContainer.appendChild(clipWrap);
      }
      return;
    }

    const wrap = document.createElement('div');
    const btnPrev = document.createElement('button');
    const btnNext = document.createElement('button');
    wrap.classList.add('container');
    btnNext.classList.add('btn-prev');
    btnPrev.classList.add('btn-next');


    wrap.innerHTML = this.card.map(card => `<div class = 'clip-wrap'>
    <img class = 'prev-img' src=${card.thumbnails.medium.url}>
    <div class = 'clip-title'><p>${card.title}</p></div>
    <p class = 'clip-chanel'>${card.channelTitle}</p>
    <p class = 'clip-date'>${card.publishedAt.substring(0, 10)}</p>
    <p class = 'clip-description'>${card.description}</p>
    </div>`).join('');
    document.body.appendChild(wrap);
    document.body.appendChild(btnPrev);
    document.body.appendChild(btnNext);

    //! ---------------------------Scroll------------------------------
    const C = document.querySelector('.container');
    const N = C.children.length;

    const NF = 30;
    let i = 0;
    let x0 = null;
    let locked = false;
    let w;
    let ini;
    let fin;
    let rID = null;
    let anf;

    function stopAni() {
      cancelAnimationFrame(rID);
      rID = null;
    }

    function ani(cf = 0) {
      C.style.setProperty('--i', ini + (fin - ini) * cf / anf);

      if (cf === anf) {
        stopAni();
        return;
      }

      // eslint-disable-next-line no-param-reassign
      rID = requestAnimationFrame(ani.bind(this, cf += 1));
    }

    function unify(e) { return e.changedTouches ? e.changedTouches[0] : e; }

    function lock(e) {
      x0 = unify(e).clientX;
      locked = true;
    }

    function drag(e) {
      e.preventDefault();

      if (locked) {
        const dx = unify(e).clientX - x0;
        const f = +(dx / w).toFixed(2);

        C.style.setProperty('--i', i - f);
      }
    }

    function move(e) {
      if (locked) {
        const dx = unify(e).clientX - x0;
        const s = Math.sign(dx);
        let f = +(s * dx / w).toFixed(2);

        ini = i - s * f;

        if ((i > 0 || s < 0) && (i < N - 1 || s > 0) && f > 0.2) {
          i -= s;
          f = 1 - f;
        }

        fin = i;
        anf = Math.round(f * NF);
        ani();
        x0 = null;
        locked = false;
      }
    }

    function size() { w = window.innerWidth; }

    size();
    C.style.setProperty('--n', N);

    window.addEventListener('resize', size, false);

    C.addEventListener('mousedown', lock, false);
    C.addEventListener('touchstart', lock, false);

    C.addEventListener('mousemove', drag, false);
    C.addEventListener('touchmove', drag, false);

    C.addEventListener('mouseup', move, false);
    C.addEventListener('touchend', move, false);

    let btnNextCoutn = i + 1;
    let btnPrevCount = i;

    btnNext.addEventListener('click', () => {
      C.style.setProperty('--i', i += 1);
      btnNextCoutn += 1;
      btnPrevCount += 1;
      btnNext.innerText = btnNextCoutn;
      btnPrev.innerText = btnPrevCount;
    });

    btnPrev.addEventListener('click', () => {
      if (i < 1) {
        lock();
      } else {
        C.style.setProperty('--i', i -= 1);
        btnNextCoutn -= 1;
        btnPrevCount -= 1;
        btnNext.innerText = btnNextCoutn;
        btnPrev.innerText = btnPrevCount;
      }
    });
  }
}
